const ADD_LIST = 'organizer/dealReducer/ADD-LIST';
const ADD_DEAL = 'organizer/dealReducer/ADD-DEAL';
const CHANGE_NAME = 'organizer/dealReducer/CHANGE-NAME';
const CHANGE_START_DATE = 'organizer/dealReducer/CHANGE-START-DATE';
const CHANGE_ENDING_DATE = 'organizer/dealReducer/CHANGE-ENDING-DATE';
const MAKE_DONE = 'organizer/dealReducer/MAKE-DONE';
const CANCEL_DONE = 'organizer/dealReducer/CANCEL-DONE';
const INIT_DATA = 'organizer/dealReducer/INIT-DATA';
const DELETE_DEAL = 'organizer/dealReducer/DELETE-DEAL';
const DELETE_LIST = 'organizer/dealReducer/DELETE-LIST';
const CHANGE_SHOW_IN_CALENDAR = 'organizer/dealReducer/CHANGE-SHOW-IN-CALENDAR';
const CHANGE_DESCRIPTION = 'organizer/dealReducer/CHANGE-DESCRIPTION';

let initialState =
    [
        {
            id: '42',
            name: 'New List',
            children: [
                {
                    id: '4242',
                    name: 'New Deal ',
                    description: '',
                    importance: false,
                    startDate: null,
                    endingDate: null,
                    isShowInCalendar: false,
                    done: false,
                    children: []
                }
            ]
        }
    ];

export const changeName = (id, newName) => ({type:CHANGE_NAME, id:id, name:newName});
export const addNewDeal = (id) => ({type:ADD_DEAL, id:id});
export const addNewList = () => ({type:ADD_LIST});
export const deleteDeal = (id) => {return({type:DELETE_DEAL, id:id})};
export const deleteList = (id) => ({type:DELETE_LIST, id:id});
export const initData = () =>  ({type:INIT_DATA});
export const makeDone = (id) => ({type:MAKE_DONE, id:id});
export const changeStartDate = (id, newDate) => ({type:CHANGE_START_DATE, id:id, startDate: newDate});
export const changeEndingDate = (id, newDate) =>({type:CHANGE_ENDING_DATE, id:id, endingDate: newDate});
export const changeShowInCalendar = (id, value) => ({type:CHANGE_SHOW_IN_CALENDAR, id:id, value:value});
export const cancelDone = (id) => ({type:CANCEL_DONE, id:id});
export const changeDescription = (id, value) => ({type:CHANGE_DESCRIPTION, id:id, value:value});

const DeployData = (data) => {
    let NewData = [...data];
    data.forEach((item) => {
        if(item.children.length > 0){
            NewData = [...NewData, ...item.children];
            DeployData(item.children);
        }
    });
    return NewData;
};

const ChangeDeal = (id, changing, data) => {
    return( data.map((item)=>{
        let newItem = {...item};
        if(item.id.slice(-2) === id.slice(0, 2)){
            id.length === 2
                ? newItem = {...item, ...changing}
                : newItem.children = ChangeDeal(id.slice(2), changing, newItem.children);}
        return newItem;
    }))
};

const genId = () => {
    let rand = 10 + Math.random() * (90);
    return Math.floor(rand)+'';
};

const addSubDeal = (id, data) => {
    return (data.map((item) => {
        let newItem = {...item};
        if (item.id.slice(-2) === id.slice(0, 2)) {
            if (id.length === 2) {
                newItem.children = addItem(item.children, 'deal', id );
            }
            else if (id.length > 2){
                newItem.children = addSubDeal(id.slice(2), newItem.children);
            }
        }
        return newItem;
    }))
};

const addItem = (data = [], itemType, parentId='' ) => {
    let newItem;
    if (itemType === 'list'){
        newItem = {
            name: 'New list',
            children: []
        }
    }
    if (itemType === 'deal'){
        newItem = {
            name: 'New Deal ',
            description: '',
            importance: false,
            startDate: null,
            endingDate: null,
            isShowInCalendar: false,
            done: false,
            children: []
        }
    }
    if (data.length < 99) {
        let newId;
        do {
            newId = genId();
        }
        while (data.findIndex(item => item.id.slice(2) === newId) !== -1);
        return [...data, {...newItem, id: parentId + newId}];
    }
    else{return [...data]}
};

const deleteItem = (id, data=[]) => {
    return (data.map((item) => {
        if (item.id.slice(-2) === id.slice(0, 2)) {
            if (id.length > 4) {
                deleteItem(id.slice(2), item.children);
            } else {
                item.children = item.children.filter(item => item.id.slice(-2) !== id.slice(2))
            }
        }
        return {...item};
    }))
};

export const DealReducer = (state = initialState, action) => {
        switch(action.type) {
            case ADD_LIST: {
                return addItem(state , 'list')
            }
            case ADD_DEAL: {
                return addSubDeal(action.id, state)
            }
            case CHANGE_NAME: {
                let changing = {name: action.name};
                return ChangeDeal(action.id, changing, state)
            }
            case CHANGE_START_DATE: {
                console.log(action.startDate);
                let changing = {startDate: new Date(action.startDate).getTime() };

                return ChangeDeal(action.id, changing, state);
            }
            case CHANGE_ENDING_DATE: {
                console.log(action.endingDate);
                let changing = {endingDate: new Date(action.endingDate).getTime()};

                return ChangeDeal(action.id, changing, state);
            }
            case DELETE_DEAL: {
                return deleteItem(action.id, state);
            }
            case DELETE_LIST: {
                if (state.length > 1) {return[...state.filter(item => item.id !== action.id)]}
                else {return [...state]}
            }
            case MAKE_DONE: {
                let changing = {done: true};
                return ChangeDeal(action.id, changing, state)
            }
            case CANCEL_DONE: {
                let changing = {done: false};
                return ChangeDeal(action.id, changing, state)
            }
            case CHANGE_SHOW_IN_CALENDAR: {
                let changing = {isShowInCalendar: action.value};
                return ChangeDeal(action.id, changing, state)
            }
            case CHANGE_DESCRIPTION: {
                let changing = {description: action.value};
                return ChangeDeal(action.id, changing, state)
            }
            case INIT_DATA: {
                if(!! JSON.parse(localStorage.getItem('userData'))){
                    return JSON.parse(localStorage.getItem('userData'))
                }
                else{
                    return [...state];
                }
            }
            default: {
                return [...state]
            }
        }
};

